import React, { useState } from "react";
import { Heading, TextInput } from "grommet";
import Box from "../components/Box";
import Button from "../components/Button";
import { createStore, createObject } from "../library/parse";

const Component = ({ number, iconOnly, text = "" }) => {
  const [storeName, setStoreName] = useState("");
  return (
    <Box>
      <Heading level={3}>Give it a name</Heading>
      <TextInput
        placeholder="name"
        value={storeName}
        onChange={(event) => setStoreName(event.target.value)}
      />
      <Box direction="row">
        <Button
          primary
          label="Setup"
          onClick={() =>
            storeName &&
            createObject("Store", {
              name: storeName,
            })
          }
        />
      </Box>
    </Box>
  );
};

export default Component;
